.movie {
    background-color: rgb(255, 255, 255);
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color: ghostwhite;
    display: grid;
    grid-template-columns: minmax(150px, 1fr) 2fr;
    grid-gap: 20px;
    text-decoration: none;
    color: inherit;
    box-shadow: 0 13px 27px -5px rgba(0, 255, 72, 0.625),
        0 8px 16px -8px rgba(0, 255, 115, 0.607),
        0 -6px 16px -6px rgba(82, 255, 116, 0.277);
}

.movie___img {
    position: relative;
    top: -50px;
    max-width: 150px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie___title {
    margin: 0;
    font-weight: 300;
    text-decoration: none;
}

.movie___title a { /*movie___title class의 child 중 <a> tag에만 적용*/
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
    text-decoration: none;
}

.movie___genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap:wrap;
    margin: 5px 0px;
}

.movie___genres li {
    margin-right: 10px;
    font-size: 14px;
}